import * as React from 'react';
import { useRef } from 'react';
import { MDXProvider } from '@mdx-js/react';
import { StaticImage } from 'gatsby-plugin-image';
import { useSiteMetadata } from '../hooks/use-site-metadata';
import { IconBars } from './icons';
import { GDPR } from './gdpr';

const shortcodes = {};

export default function Layout({ children }) {
  const navigationRef = useRef(null);
  const { title, description } = useSiteMetadata();

  function handleToggleMenu() {
    navigationRef.current.classList.toggle('hidden');
  }

  return (
    <MDXProvider components={shortcodes}>
      <GDPR />
      <div className="lg:flex">
        <header className="top-0 bottom-0 overflow-y-auto lg:fixed lg:w-96 lg:bg-sidebar">
          <div className="flex justify-between bg-sidebar p-12 text-white">
            <div>
              <a className="text-2xl font-bold" href="/">
                {title}
              </a>
              <p className="text-sm text-gray-300">{description}</p>
            </div>
            <button className="lg:invisible" onClick={handleToggleMenu} aria-label="Menu">
              <IconBars />
            </button>
          </div>
          <nav
            ref={navigationRef}
            className="hidden bg-white sm:m-12 sm:mb-2 sm:py-4 lg:m-2 lg:block lg:bg-sidebar lg:p-0 lg:text-white">
            <Navigation />
          </nav>
        </header>
        <main className="max-w-[825px] bg-white sm:m-12 lg:ml-[432px]">
          <StaticImage src={'../images/header.png'} alt={title} />
          <article className="prose prose-sm max-w-none p-8 md:prose-base">{children}</article>
        </main>
      </div>
    </MDXProvider>
  );
}

function Navigation() {
  return (
    <>
      <div>
        <ul className="mx-8">
          <li className="p-3">
            <a href="/">Hlavná stránka</a>
          </li>
          <li className="border-b lg:border-violet-900"></li>
          <li className="p-3">
            <a href="/kategoria/astrologicky-horoskop">Astrologický horoskop</a>
          </li>
          <li className="border-b lg:border-violet-900"></li>
          <li className="p-3">
            <a href="/kategoria/cinsky-horoskop">Čínsky horoskop</a>
          </li>
          <li className="border-b lg:border-violet-900"></li>
          <li className="p-3">
            <a href="/kategoria/tarotove-karty">Tarotové karty</a>
          </li>
          <li className="border-b lg:border-violet-900"></li>
          <li className="p-3">
            <a href="/kategoria/numerologia">Numerológia</a>
          </li>
        </ul>
      </div>
    </>
  );
}
